import React from 'react';
import { graphql } from 'gatsby';
import { Helmet } from "react-helmet"
import Tag from '../components/Tag';
import Nav from '../components/Nav';

require(`katex/dist/katex.min.css`)
require("../styles/prism-base16-ateliersulphurpool.light.css")

const Template = ({data}) => {
  const {markdownRemark} = data
  const html = markdownRemark.html
  const date = markdownRemark.frontmatter.date
  const tags = markdownRemark.frontmatter.tags
  const title = markdownRemark.frontmatter.title
  
  return (
    <section>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{title}</title>
      </Helmet>

      <div className="container">
        <div className="columns">
          <div className="column" />
          <div className="column is-half" style={{padding: "0.75rem"}}>
          <Nav />
            <br />
            <div className="generated">
              <p className="is-size-7" style={{marginBottom: "0.5rem"}}>
                Tags: {tags.map(tag => <Tag name={tag} />)} 
              </p>
              <p className="is-size-7" style={{marginBottom: "0.5rem"}}>Written: {date}</p>
              <div style={{marginTop: "1rem"}} dangerouslySetInnerHTML={{__html: html}}/>
            </div>
            <hr style={{marginTop: "2.5rem"}}/>
            <div className="level">
              <a href="/blog"><span class="tag is-link is-light is-medium">Back To All Posts</span></a>
            </div>
          </div>
          <div className="column" />
        </div>
      </div>
    </section>
  )
}

export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: {eq: $pathSlug }}) {
      rawMarkdownBody
      html
      frontmatter {
        title
        date
        tags
        excerpt
        img
      }
    }
  }
`

export default Template;